/* Header section */

.header_div {
    /* width: 100vw; */
    height: 80vh;
    background-color: #75cfb8;
}

.intro {
    display: flex;
    flex-direction: row;
    position: relative;
    top: 30vh;
    color: white;
    justify-content: space-evenly;
    align-items: center;
}

.intro_text {
    width: 25rem;
}

/* Live search */

.live_search_div {
    display: flex;
    flex-direction: column;
    width: 40%;
    margin: 1rem auto 1rem auto;
    position: relative;
}

.search_items {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: none;
    position: absolute;
    top:2.5rem;
    width: 100%;
    z-index: 1;
    opacity: 1;
    background-color: white;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
}

.search_items li {
    list-style: none;
    padding: 0.8rem;
    cursor: pointer;
    width: 100%;
    border-top: none;
}

.search_items li:hover{
    background-color: #dddddd;
}

.live_search_div input {
    width: 100%;
    border: 0.5px solid #f6f5f5;
    border-right: none;
    padding: 0.8rem;
    height: 2.5rem;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    outline: none;
    color: black;
}

.searchButton {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 0 rgba(0, 0, 0, 0.19);
    /* background:#00B4CC ; */
    background: #393e46;
    border: none;
    color: #fff;
    cursor: pointer;
}

.searchButton:focus{
    border: none;
    outline: none;
}


/*  */

.blog-card{
    display: flex;
    flex-direction: row;
    width: 90%;
    position: relative;
    margin: 1rem auto 1rem auto;
    border: 1px solid #e7e6e1;
    border-radius: 15px;
    padding: 0.8rem;
    padding-bottom: 0.3rem;
    align-items: center;
}

.blog-img{
    width: 250px;
    object-fit:contain;
    margin-right: 1rem;
}

.blog_des{
     margin-bottom: 0.2rem;
     cursor: pointer ;
     font-weight:400
}

/*  */


.dropdown:hover .options-content {
    display: flex;
    flex-direction: column;
}

.options-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 1;
}

.blogHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.blog-des a {
    text-decoration: underline;
}

/* cover image file input */
.cover-input {
    display: none;
}

.custom-file-upload {
    border: 1px solid #ccc;
    display: inline-block;
    padding: 6px 12px;
    cursor: pointer;
}

/* Pagination section */

.page-ul {
    display: flex;
    flex-direction: row;
    list-style-type: none;
    justify-content: center;
}

.page-li {
    text-decoration: none;
    background-color: #fff;
    border-style: solid;
    border-width: thin;
    padding: 0.25rem 0.5rem 0.25rem 0.5rem;
    border-color: #dee3e2;
}

.page-number {
    text-decoration: none;
    color: #0779e4;
}

.confeti {
    left: 50%;
}

/* button */

.new-blog-button {
    /* color: rgb(54, 54, 54); */
    color: white;
    background-color: #393e46;
    display: block;
    margin: auto;
    padding: 10px 20px;
    font-size: 1.3rem;
    font-weight: 500;
    /* border: 2px solid rgb(212, 212, 212); */
    border: none;
    border-radius: 5px;
}
